import React from 'react';
import Helmet from 'react-helmet';
import { withPrefix } from 'gatsby';
import { useSiteMetadata } from '../../hooks';
import styles from './Layout.module.scss';

const Layout = ({
  children,
  title,
  description,
  socialImage,
  canonicalPath,
  ogType = 'website'
}) => {
  const { author, url, title: siteTitle } = useSiteMetadata();
  const metaImage = socialImage != null ? socialImage : author.photo;
  const metaImageUrl = url + withPrefix(metaImage);
  const canonicalUrl = (() => {
    if (canonicalPath) return url + withPrefix(canonicalPath);
    if (typeof window !== 'undefined') return url + withPrefix(window.location.pathname);
    return url + withPrefix('/');
  })();
  const twitterHandle = author?.contacts?.twitter ? `@${author.contacts.twitter.replace(/^@/, '')}` : undefined;
  const websiteLdJson = {
    '@context': 'https://schema.org',
    '@type': 'WebSite',
    url,
    name: siteTitle,
    publisher: {
      '@type': 'Person',
      name: author?.name
    }
  };

  return (
    <div className={styles.layout}>
      <Helmet>
        <html lang="en" />
        <title>{title}</title>
        <meta name="viewport" content="width=device-width, initial-scale=1" />
        <meta name="description" content={description} />
        <meta name="robots" content="index,follow" />
        <link rel="canonical" href={canonicalUrl} />
        <link rel="alternate" type="application/rss+xml" title={`${siteTitle} RSS Feed`} href={url + withPrefix('/rss.xml')} />
        <meta property="og:site_name" content={siteTitle} />
        <meta property="og:type" content={ogType} />
        <meta property="og:url" content={canonicalUrl} />
        <meta property="og:title" content={title} />
        <meta property="og:description" content={description} />
        <meta property="og:image" content={metaImageUrl} />
        <meta name="twitter:card" content="summary_large_image" />
        {twitterHandle ? <meta name="twitter:site" content={twitterHandle} /> : null}
        {twitterHandle ? <meta name="twitter:creator" content={twitterHandle} /> : null}
        <meta name="twitter:title" content={title} />
        <meta name="twitter:description" content={description} />
        <meta name="twitter:image" content={metaImageUrl} />
        <script type="application/ld+json">{JSON.stringify(websiteLdJson)}</script>
      </Helmet>
      {children}
    </div>
  );
};

export default Layout;
